import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { Button, Card, CardBody, CardGroup, Col, Container, Input, InputGroup, InputGroupAddon, InputGroupText, Row } from 'reactstrap';
import { Field, Form } from 'react-final-form';
import { RenderInput, RenderInputIcon, LoadingButton, ErrorToast, FloatToast } from '../../../components';
import { addAdmin, loginAdmin } from '../../../handlers/Api/Calls';
import { useHistory } from 'react-router-dom';

const Login = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [data, setData] = useState({});
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(false);
  const history = useHistory();

  const onSubmit = values => {
    loginAdmin(values, (loginLoading, loginData, loginError, loginSuccess) => {
      setIsLoading(loginLoading);
      setData(loginData);
      setError(loginError);
      setSuccess(loginSuccess);
      if (loginSuccess) {
        localStorage.setItem('user', JSON.stringify(values));
        localStorage.setItem('token', JSON.stringify(loginData));
        history.push('/dashboard');
      }
    });
  }

  return (
    <div className="app flex-row align-items-center">
      <Container fluid>
        {success && <FloatToast message="User login successfuly" />}
        {error && <ErrorToast error={error} />}
        <Row className="justify-content-center">
          <Col md="8">
            <CardGroup>
              <Card className="p-4">
                <CardBody>
                  <Form onSubmit={onSubmit}>
                    {props => (
                      <form onSubmit={props.handleSubmit}>
                        <h1>Login</h1>
                        <p className="text-muted">Sign In to your account</p>
                        <Field
                          name="username"
                          placeholder="Username"
                          autoComplete="username"
                          icon={<i className="icon-user"></i>}
                          component={RenderInputIcon}
                        />
                        <Field
                          name="password"
                          type="password"
                          placeholder="Password"
                          autoComplete="password"
                          icon={<i className="icon-lock"></i>}
                          component={RenderInputIcon}
                        />
                        <Row>
                          <Col xs="6">
                            <LoadingButton
                              color="primary"
                              className="px-4"
                              loading={isLoading}
                            >Login</LoadingButton>
                          </Col>
                          <Col xs="6" className="text-right">
                            <Button color="link" className="px-0">Forgot password?</Button>
                          </Col>
                        </Row>
                      </form>
                    )}
                  </Form>
                </CardBody>
              </Card>
              <Card className="text-white bg-primary py-5" style={{ width: '44%' }}>
                <CardBody className="text-center">
                  <div>
                    <h2>Sign up</h2>
                    <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut
                        labore et dolore magna aliqua.</p>
                    <Link to="/register">
                      <Button color="primary" className="mt-3" active tabIndex={-1}>Register Now!</Button>
                    </Link>
                  </div>
                </CardBody>
              </Card>
            </CardGroup>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default Login;
